import { useReducer, useCallback } from 'react'
import { IDiagramType } from '../types'

// useReducerstate
const initialState = {
  // state
  past: [],
  // state
  present: null,
  // future
  future: [],
}

// actionstate
const reducer = (state: any, action: any) => {
  const { past, present, future } = state
  const { newPresent } = action

  switch (action.type) {
    case 'UNDO':
      const previous = past[past.length - 1]
      const newPast = past.slice(0, past.length - 1)

      return {
        past: newPast,
        present: previous,
        future: [present, ...future],
      }
    case 'REDO':
      const next = future[0]
      const newFuture = future.slice(1)

      return {
        past: [...past, present],
        present: next,
        future: newFuture,
      }
    case 'SET':
      return {
        past: [...past],
        present: newPresent,
        future: [],
      }

    case 'SET_WIDTH_HISTORY':
      if (newPresent === present) {
        return state
      }
      return {
        past: [...past, present],
        present: newPresent,
        future: [],
      }

    case 'ADD_A_HISTORY':
      if (newPresent === present) {
        return state
      }
      return {
        past: [...past, newPresent],
        present: present,
        future: [],
      }

    case 'CLEAR':
      const { initialPresent } = action

      return {
        ...initialState,
        present: initialPresent,
      }
  }
}

// Hook
export const useHistory = (initialPresent: IDiagramType) => {
  const [state, dispatch] = useReducer(reducer, {
    ...initialState,
    present: initialPresent,
  })

  const canUndo = state.past.length !== 0
  const canRedo = state.future.length !== 0

  const undo = useCallback(() => {
    if (canUndo) {
      dispatch({ type: 'UNDO' })
    }
  }, [canUndo, dispatch])

  const redo = useCallback(() => {
    if (canRedo) {
      dispatch({ type: 'REDO' })
    }
  }, [canRedo, dispatch])

  //     node
  const set = useCallback((newPresent) => dispatch({ type: 'SET', newPresent }), [dispatch])

  //    data
  const setWithHistory = useCallback((newPresent) => dispatch({ type: 'SET_WIDTH_HISTORY', newPresent }), [dispatch])

  //    
  const addAHistory = useCallback((newPresent) => dispatch({ type: 'ADD_A_HISTORY', newPresent }), [dispatch])

  const clear = useCallback(() => dispatch({ type: 'CLEAR', initialPresent }), [dispatch, initialPresent])

  return { present: state.present, set, setWithHistory, addAHistory, undo, redo, clear, canUndo, canRedo }
}
